import React, { Component } from 'react';
import {
  Link
} from 'react-router-dom';

class Blog extends Component {
  render() {
    return (
        <article className="bt bb b--black-10">
          <Link to="/blog-detail" className="db pv4 ph3 ph0-l no-underline black dim">
            <div className="flex flex-column flex-row-ns">
              <div className="pr3-ns mb4 mb0-ns w-100 w-40-ns">
                <img src={this.props.logo} className="db" alt={this.props.logoDesc}></img>
              </div>
              <div className="w-100 w-60-ns pl3-ns">
                <h1 className="f3 fw1 baskerville mt0 lh-title">{this.props.title}</h1>
                <p className="f6 f5-l lh-copy">
                  {this.props.desc}
                </p>
                <p className="f6 lh-copy mv0">{this.props.author}</p>
              </div>
            </div>
          </Link>
        </article>
    );
  }
}

export default Blog;



