import React, { Component } from 'react';
import Blog from './blog';


import DeviceTrackerLogo from './device-tracker.png';

const title = 'Angular 2 Introduction';
const description = 'We will walk through a simple authentication system implemented in Angular2 , NodeJS. This comprises' +
' of Angular2 components , services, validations , using tachyons-css to create simple login and sign up screens. We will follow' +
' a test first approach and start by writing test cases in Jasmine with Karma as test runner.' ;

class Angular2Intro extends Component {
  render() {
    return (

      <Blog title={title} desc={description} author="Sanket Maru" logo={DeviceTrackerLogo} />
    );
  }
}

export default Angular2Intro;



