import React, { Component } from 'react';
import Highlight from 'react-highlight';

class BlogDetail extends Component {
  render() {
    return (
      <article className="center pa3 pa5-ns">
        <h1 className="measure-wide center">Angular 2 Introduction</h1>
        <p className="measure-wide   center lh-copy">
          We will create a new project using <span><a href="https://cli.angular.io/" target="_blank">angular-cli </a></span>
          and follow the commands as per the docs.
          <br/>
          <br/>
          Once you have created a new project and started the server using ng serve , you should have
          <b> app works !</b> on your browser when you hit http://localhost:4200.
          <br/><br/>
          We will kick off with creating a new login component using commands
          <br/>
          ng new component login

          <Highlight className='Javascript'>
            {`import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

interface Credentials {
  username: string,
  password: string
}`
          }
          </Highlight>


        </p>
        <p className="measure-wide center lh-copy">

        </p>
      </article>


    );
  }
}

export default BlogDetail;



